  /*v-zoomer*/
  zoomIn() {
    //@ts-ignore
    this.$refs.zoomer.zoomIn();
  }

  zoomOut() {
    //@ts-ignore
    this.$refs.zoomer.zoomOut();
  }

  setOffset(x = 0, y = 0) {
    //@ts-ignore
    this.$refs.zoomer.setOffset(x, y);
  }

  /*vue-draggable-resizable*/
  saveXY() {
    const panelSettings = {
      x: this.x,
      y: this.y,
      width: this.width,
      height: this.height,
    };
    localStorage.setItem('TradingPanelSettings', JSON.stringify(panelSettings));
  }

  onResize(x, y, width, height) {
    this.x = x;
    this.y = y;
    this.width = width;
    this.height = height;
    this.saveXY();
  }

  onDrag(x, y) {
    this.x = x;
    this.y = y;
    this.saveXY();
  }

