import {Video} from './Video'
import {PlayerState} from './Constants'

export class Player {
  public player: any = null
  public endSeconds: number = null;
  public currentVideoId: string = ''
  private interval: number = null;

  constructor(
    public  YT: any,
    public id: number,
    public  elementDom: any,
    public eventHandler: any,
    public config: any,
  ) {
    const resultingConfig = this.getPlayerConfig()
    const {videoId,autoplay} = config
    resultingConfig.videoId = videoId
    resultingConfig.playerVars.autoplay = autoplay
    this.currentVideoId = videoId
    this.player = new YT.Player(elementDom, resultingConfig);
  }

  private getPlayerConfig() {
    const Lscreen = window.innerWidth
    console.log('Lscreen',Lscreen)
    const largeur = Lscreen - 27; // add this line

    return {
      videoId: this.currentVideoId,
      width: largeur/2,
      height: (largeur * .78)/2,

      host: 'https://www.youtube.com',
      playerVars: {
        autoplay: true,
        controls: 1, // Show pause/play  buttons in player
        showinfo: 0, // Hide the video title
        modestbranding: 1, // Hide the Youtube Logo
        // fs: 1, // Hide the full screen button
        // cc_load_policy: 0, // Hide closed captions
        iv_load_policy: 3, // Hide the Video Annotations
        autohide: 1 // Hide video controls when playing
      },
      events: {
        onStateChange: this.onStateChange,
        onError: this.onError,
        onReady: this.onReady,
      },
    };
  };

  public play(video: Video) {
    this.player.playVideo()
  }

  public seek(video: Video) {
    const {end, start, videoId} = video
    if (end > 0) this.endSeconds = end
    this.player.seekTo(start);
    this.runCheckInterval()
  }

  public loadVideoById(video: Video) {
    /*    Синтаксис аргументов
        loadVideoById("bHQqvYy5KYo", 5, "large")

        Синтаксис объектов
        loadVideoById({'videoId': 'bHQqvYy5KYo',
          'startSeconds': 5,
          'endSeconds': 60,
          'suggestedQuality': 'large'});*/
    const {end, start, videoId} = video
    this.player.loadVideoById(videoId);
  }

  public stop() {
    this.player.pauseVideo()
  }

  private onStateChange = (event) => {
    this.eventHandler({id: this.id, state: event.data})
  };

  private onReady = (event) => {
    this.eventHandler({id: this.id, state: PlayerState.READY})
  };

  private onError = (error) => {
    this.eventHandler({id: this.id, state: PlayerState.ERROR, info: error})
  };

  private clearCheckInterval() {
    clearInterval(this.interval)
    this.interval = null
  }

  private runCheckInterval = () => {
    if (!this.interval) {
      this.interval = setInterval(this.checkEndSeconds.bind(this), 250)
    }
  }

  private checkEndSeconds() {
    const currentTime = this.player.getCurrentTime()
    if (Number.isInteger(this.endSeconds) && currentTime >= this.endSeconds) {
      this.endSeconds = null
      this.clearCheckInterval()
      // this.player.pauseVideo()
      this.eventHandler({id: this.id, state: PlayerState.ENDED, info: currentTime})
    }
  }

}
